.note{
	position: relative;
	background-color: var(--dark);
	padding: 16px;
	border-radius: 16px;
	display: flex;
	align-items: center;
	transition: 0.5s;
	cursor: pointer;
	margin-bottom: 16px;
    font-size: 1.75rem;
}

.note:hover {
	opacity: 0.7;
}
.note .checkbox {
	width: 20px;
	height: 20px;
	border-radius: 50%;
	background-color: var(--dark-alt);
	background-image: linear-gradient(to bottom, transparent, transparent);
	transition: 0.4s;
    position: absolute;
    right: 1rem;

}

@media only screen and (max-width: 500px) {
    h1{
        text-align: center;
    }
    li{
        right: 1rem;
    }
    .note{
        font-size: 1.2rem;
        position: relative;
        background-color: var(--dark);
        padding: 26px;
        border-radius: 16px;
        display: flex;
        align-items: center;
        transition: 0.5s;
        cursor: pointer;
        margin-bottom: 26px;
    }
}
    .note .checkbox {
        width: 15px;
        height: 15px;
        border-radius: 50%;
        background-color: var(--dark-alt);
        background-image: linear-gradient(to bottom, transparent, transparent);
        transition: 0.4s;
        position: absolute;
        right: 0.75rem;    
    }
